import * as React from 'react';
import { observer, inject } from 'mobx-react';
import { Button, Layout, Modal, Drawer } from "antd";
import { WrappedFormUtils } from 'antd/lib/form/Form'
import './css/edit.css'

const ReactMarkdown = require('react-markdown')
const { confirm } = Modal;

interface Props {
    question: any,
    location: any,
    form: WrappedFormUtils,
    examMsg: any,
    history: any,
    values: any
}

@inject('examMsg')
@observer

class Edit extends React.Component<Props> {
    constructor(props: Props) {
        super(props)
    }
    state = {
        list: {
            title: '',
            end_time: 0,
            start_time: 0,
            questions: [],
            exam_exam_id: ''
        },
        questions: [],
        visible: false
    }

    componentDidMount() {
        this.getlist()
    }

    // 
    getlist = () => {
        const data = this.props.location.state.data
        this.setState({ list: { ...data }, questions: data.questions })
    }

    // 
    showDeleteConfirm = (id: any, i: number) => {
        const that = this
        confirm({
            title: '',
            content: '',
            okText: 'Yes',
            okType: 'danger',
            cancelText: 'No',
            async onOk() {
                const { questions } = that.state.list
                questions.splice(i, 1)
                that.setState({ questions })
                const data = await that.props.examMsg.delExam(id)
            },
            onCancel() {
                console.log('Cancel');
            },
        });
    }

    // 
    addexam = async (questions: any) => {
        const arr: any = []
        questions.map((item: any) => {
            return arr.push(item.questions_id)
        })
        const str = JSON.stringify(arr.join(''))
        const { exam_exam_id } = this.state.list
        const obj = { question_ids: str }
        const data = await this.props.examMsg.updateExam(exam_exam_id, obj)
        this.props.history.replace({
            pathname: '/home/list',
            state: { title: '' }
        })
    }

    // 
    showDrawer = () => {
        this.setState({
            visible: true,
        });
    };

    // 
    onClose = () => {
        this.setState({
            visible: false,
        });
    };

    render() {
        const { title, end_time, start_time, questions, exam_exam_id } = this.state.list
        const time = new Date(end_time - start_time).toTimeString()
        const start = new Date(start_time).toLocaleString()
        return (
            <Layout className="addquersition">
                <h2></h2>

                <div className="editBox">
                    <Button onClick={this.showDrawer}></Button>
                    <Drawer
                        title=""
                        placement="right"
                        closable={false}
                        onClose={this.onClose}
                        visible={this.state.visible}
                    >
                        <p>Some contents...</p>
                        <p>Some contents...</p>
                        <p>Some contents...</p>
                    </Drawer>
                    <div className="content">
                        <h2>{title}</h2>
                        <p>{time}  {start} </p>
                        {
                            this.state.questions.map((item: any, i: number) => {
                                return <div className="everyItem" key={i}>
                                    <h4>{i + 1}: {item.title} <a onClick={() => { this.showDeleteConfirm(exam_exam_id, i) }} type="dashed"></a></h4>
                                    <ReactMarkdown source={item.questions_stem} />
                                </div>
                            })
                        }
                        <Button onClick={() => { this.addexam(questions) }} className="btn"></Button>
                    </div>
                </div>

            </Layout>
        );
    }

}

export default Edit;



